import React, { useEffect, useState } from 'react';
import {
  ResponsiveContainer,
  ScatterChart,
  Scatter,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
} from 'recharts';
import './App.css';
import './nprogress.css';

//App Components
import { EventList } from './EventList';
import { CitySearch } from './CitySearch';
import { NumberOfEvents } from './NumberOfEvents';

import { getEvents, extractLocations } from './api';
import { WarningAlert } from './Alert';

export default function App() {
  let [events, setEvents] = useState([]),
    [locations, setLocations] = useState([]),
    [displayEvents, setDisplayEvents] = useState([]),
    [selectedLocation, setSelectedLocation] = useState(''),
    [eventCount, setEventCount] = useState(16),
    [warningText, setWarningText] = useState('');

  const handleOnlineStatus = () => {
    !navigator.onLine
      ? setWarningText(
          'You are currently offline, events may not be up-to-date.'
        )
      : setWarningText('');
  };

  const getData = () => {
    const data = locations.map((location) => {
      const number = events.filter(
        (event) => event.location === location
      ).length;
      const city = location.split(', ').shift();
      return { city, number };
    });
    return data;
  };

  useEffect(() => {
    handleOnlineStatus();

    getEvents().then((response) => {
      setEvents(response);
      setDisplayEvents(response);
      setLocations(extractLocations(response));
    });
  }, []);

  useEffect(() => {
    handleOnlineStatus();

    const relevantEvents = (
      selectedLocation === ''
        ? events
        : events.filter((event) => event.location === selectedLocation)
    ).slice(0, eventCount || 16);
    setDisplayEvents(relevantEvents);
  }, [selectedLocation, eventCount, events]);

  return (
    <div className="app">
      <CitySearch
        locations={locations}
        setSelectedLocation={setSelectedLocation}
      />
      <WarningAlert text={warningText} />

      <ResponsiveContainer height={400}>
        <ScatterChart margin={{ top: 20, right: 20, bottom: 10, left: 10 }}>
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis
            dataKey="city"
            name="City"
            type="category"
            angle={-45}
            textAnchor="end"
          />
          <YAxis
            dataKey="number"
            name="Number of Events"
            type="number"
            allowDecimals={false}
          />
          <Tooltip cursor={{ strokeDasharray: '3 3' }} />
          <Scatter data={getData()} fill="#8884d8" />
        </ScatterChart>
      </ResponsiveContainer>
      <EventList events={displayEvents} />
      <NumberOfEvents eventCount={eventCount} setEventCount={setEventCount} />
    </div>
  );
}
